import React from "react"
import { Link } from "gatsby"
import scrollTo from "gatsby-plugin-smoothscroll"

import backgroundImg from "../img/background3.png"
import nameGIF from "../gifs/myname.gif"
import contactGIF from "../gifs/contact.gif"
import projectsGIF from "../gifs/projects.gif"
import whoamiGIF from "../gifs/whoami.gif"
import Whoami from "./whoami"

import Footer from "../components/footer"
import indexStyle from "./index.module.scss"
import whoamiStyle from "./whoami.module.scss"

const IndexPage = () => {
    return (
      <div>
        <div className={indexStyle.container}>
          <img
            className={indexStyle.backgroundImg}
            src={backgroundImg}
            alt="background"
            width="100%"
          />

          <img className={indexStyle.nameImg} src={nameGIF} alt="my name" />

          <div className={indexStyle.navi}>
            <Link to="/projects">
              <img
                href="/projects"
                className={indexStyle.projectsImg}
                src={projectsGIF}
                alt="my name"
              />
            </Link>

            <Link to="/contact">
              <img
                className={indexStyle.contactImg}
                src={contactGIF}
                alt="my name"
              />
            </Link>
            <div
              className={indexStyle.whoami}
              onClick={() => scrollTo("#whoami")}
            >
              <img
                className={indexStyle.whoamiImg}
                src={whoamiGIF}
                alt="my name"
              />
            </div>
          </div>
        </div>
        <div className={whoamiStyle.container}>
          <div id="whoami">
            <Whoami />
          </div>
        </div>
        <Footer />
      </div>
    )
}
 
export default IndexPage;